<template>
<div>
  <h1>Test</h1>
  <h2>{{product.title}}</h2>
  <router-link to="/greetings">Back</router-link>
</div>
</template>

<script>
export default {
  name: 'product',
  data: () => {
    return {
      product: ''
    }
  },

  // beforeRouteEnter (to, from, next) {
  //   fetch(`https://www.everyonedoesit.ca/products/${to.params.handle}.json`)
  //     .then(response => response.json())
  //     .then(result => {
  //       console.log(result)
  //     })
  //     .catch(err => console.log(err.message))
  //   return next()
  // }

  mounted () {
    fetch(`https://www.everyonedoesit.ca/products/${this.$route.params.handle}.json`)
      .then(response => response.json())
      .then(result => {
        this.product = result.product
        console.log(this.product)
      })
      .catch(err => console.log(err.message))
  }
}
</script>



