<template>
  <div>
    <div>
      <!-- <button @click="getCollection">Image</button> -->
      <button @click="logout">Logout</button>
    </div>
    <span v-for="product in products" :key="product.id">
      <img
        v-for="image in product.images"
        :key="image.id"
        :src="image.src"
        width="150"
        height="150"
      />
      <h3>{{product.title}}</h3>
      <!-- <router-link :to="'/product/' + product.handle">More Info</router-link> -->
      <router-link :to="{name: 'Product', params: { handle: product.handle }}">More Info</router-link>
    </span>
  </div>
</template>

<script>
import firebase from 'firebase'

export default {
  name: 'greetings',
  data: () => {
    return {
      greetings: 'Welcome inside!',
      howMany: 10,
      imgWidth: 150,
      imgHeight: 300,
      collection: 'random',
      indexTop: 1000,
      products: null
    }
  },

  methods: {
    logout () {
      firebase.auth().signOut().then(() => {
        this.$router.replace('login')
      })
    }

    // getCollection () {
    // }
  },

  mounted () {
    fetch(`https://www.everyonedoesit.ca/products.json`)
      .then(response => response.json())
      .then(result => {
        console.log(result.products)
        this.products = result.products
      })
      .catch(err => console.log(err.message))
  }
}
</script>



